<template>
  <div class="channel">
  
    <top-tab></top-tab>

    <van-pull-refresh v-model="refreshing" @refresh="onRefresh">
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <van-grid :border="false" :column-num="3">
          <van-grid-item v-for="item in list" :key="item" :title="item">
            <div class="live-item">
              <div class="live-img">
                <img src="https://img.yzcdn.cn/vant/apple-1.jpg" alt="封面图">

                <div class="live-title">
                  <div class="user-info">
                    <span class="user-name">英雄联盟</span>
                    <span class="user-title">xxx个直播</span>
                  </div>
                </div>
              </div>
            </div>
          </van-grid-item>
        </van-grid>
      </van-list>
    </van-pull-refresh>

  </div>
  
</template>

<script>
import { mapState, mapMutations } from "vuex";
import {log} from '../utils/log.js'
export default {
  name: "channel",
  components: {
    // SideBar,
  },
  data() {
    return {
      active: 0,
      list: [],
      loading: false,
      finished: false,
      refreshing: false,
    };
  },
  created() {
    let path = this.$route.path
    this.setState({'path': path})
    this.setState({'isShowSetPage': false})
  },
  computed: {
    ...mapState({
      home: state => state.home
    })
  },
  watch: {
    // cityname: function() {
    //   log('watch cityname this.home.city ', `(${this.cityname})`, `(${this.home.city})`)
    //   let area = this.getcitycode(this.cityname)
    //   this.area = area
    //   this.setState({'area': this.area})
    // },
  },

  activated() {

  },
  
  mounted() {

  },
  methods: {
    ...mapMutations(["setState"]),
    // getcityname(citycode) {
    //   for (const i in this.citylist) {
    //     if (citycode == this.citylist[i].code) {
    //       this.cityname = this.citylist[i].name;
    //     }
    //   }
    // },
    onLoad() {
      setTimeout(() => {
        if (this.refreshing) {
          this.list = [];
          this.refreshing = false;
        }

        for (let i = 0; i < 5; i++) {
          this.list.push(this.list.length + 1);
        }
        this.loading = false;

        if (this.list.length >= 8) {
          this.finished = true;
        }
      }, 1000);
    },
    onRefresh() {
      // 清空列表数据
      this.finished = false;

      // 重新加载数据
      // 将 loading 设置为 true，表示处于加载状态
      this.loading = true;
      this.onLoad();
    },
  },
  
};
</script>

<style lang="scss" scoped>

  .top-tab {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: aquamarine;
  }

  .live-item {
    width: 31vw;
    .live-img {
      background-color: burlywood;
      // position: relative;
      img {
        height: 150px;
        // width: 30vw;
      }
      .live-title {
        display: flex;
        padding-bottom: 2px;
        .user-info {
          display: flex;
          flex-direction: column;
          .user-name {
            font-size: 14px;
            font-weight: 700;
          }
          .user-title {
            font-size: 10px;
            color: #8b8b8b;
          }
        }
      }
    }
  }


  .channel {
    background-color: #eeeeee;
    /deep/ .van-nav-bar::after {
      border: unset;
    }
    /deep/ .van-grid {
      margin-top: 46px;
    }
      /deep/ .van-grid-item__content {
      padding: unset;
      margin: 4px 0;
      background-color: transparent;
    }
    /deep/ .van-tabs__wrap {
      height: 40px;
    }
    /deep/ .van-tab {
      line-height: 40px;
    }
    /deep/ .van-sticky {
      margin-top: 40px;
      /deep/ .van-tabs__wrap::after {
        border: unset;
      }
    }
    /deep/ .van-icon {
      color: #8b8b8b;
    }
  }
  
</style>
